import React,{useState}  from 'react';
import Formm from '../../Component/form'
import Head from '../../Component/head.js';
import useInput from '../../hook/custom-hook.js';

import axios from 'axios';
import dynamic from 'next/dynamic';
import 'react-quill/dist/quill.snow.css';
const DynamicReactQuill = dynamic(() => import('react-quill'));
import {  Col, 
          Button, 
          Form, 
          FormGroup, 
          Label, 
          Input, 
          FormText,
          Nav, 
          NavItem, 
          NavLink,
          Container,
          Row  } 
          from 'reactstrap';
          
export default function adminDashBoardCourse() {




  const[getcourseName,setcourseName] = useState("") ;
  const[getcourseImage,setcourseImage] = useState("") ;
  const[getcourseActive,setcourseActive] = useState("") ;
  const[getcourseDetail,setcourseDetail] = useState("") ;

function handleChangecourseName(inputName) {
  setcourseName(inputName.target.value)
  console.log(inputName.target.value)
}


function handleChangecourseImage(inputImage) {
  setcourseImage(inputImage.target.value)
  console.log(inputImage.target.value)
}

function handleChangecourseDetail(inputDetail) {
  setcourseDetail(inputDetail)
  console.log(inputDetail)
}   
async function handleSubmit(){
  const inputs = {
    "courseName":getcourseName,
    "courseImage":getcourseImage,
    "courseActive":getcourseActive,
    "courseDetail":getcourseDetail,
  }
  console.log(inputs)
  await axios
          .post("/api/course", inputs)
          .then(res => {
            console.log(res)
            alert("เพิ่มข้อมูลเรียบร้อยแล้ว");
          })
          .catch(err => {
            console.log(err);
          })

}  

async function handleEvent(){
  var txt;
  var event = prompt("Please add event:", "Event");
  if (event == null || event == "") {
    alert("ไม่มีข้อมูล");
  } else {
    txt = event;
  }
  document.getElementById("demo").innerHTML = txt;
}
  
  return (
    <div style={{marginTop: '20px'}}>
      <Head title="Add Course - Project Lab" />
      <Formm>
        <br/>
        <Container>
          <Row>
            <Col sm ="2">
            <div>
                    <h2>หมวดหมู่</h2>
                    <h4 style={{marginLeft:"10px",marginTop:"20px"}}>Shop</h4>
                    <Nav vertical pills>
                        <NavItem>
                            <NavLink href="/AdminDashBoard/adminDashBoardShop"  >เพิ่มสินค้า</NavLink>
                            <NavLink href="/AdminDashBoard/showItemShop"> แก้ไข/แสดงสินค้า </NavLink>
                            <NavLink href="/AdminDashBoard/order/orderShop"> Order </NavLink>
                        </NavItem>
                        <NavItem>
                        <hr />
                        <h4 style={{marginLeft:"10px"}}>Course</h4>
                            <NavLink href="/AdminDashBoard/adminDashBoardCourse" active>เพิ่ม Course</NavLink>
                            <NavLink href="/AdminDashBoard/showItemCourse">แก้ไข/แสดง Course</NavLink>
                            <NavLink href="/AdminDashBoard/order/orderCourse"> Order </NavLink>

                        </NavItem>
                        <NavItem>
                        <hr />
                        <h4 style={{marginLeft:"10px"}}>USER PROFILE</h4>
                            <NavLink href="/AdminDashBoard/userProfile/userProfile">แก้ไข</NavLink>

                        </NavItem>
                        <NavItem>
                        <hr />
                        <h4 style={{marginLeft:"10px"}}>ADMIN CONSOLE</h4>
                            <NavLink href="/AdminDashBoard/adminConsole/adminConsole">จัดการข้อมูล</NavLink>

                        </NavItem>
                    </Nav>
                </div>
            </Col>
            <Col>
              <Form>
                <h1>Add Course</h1>
                  <FormGroup row>
                    <Label for="courseName" sm={2}>Course Name : </Label>
                    <Col sm={10}>
                      <Input type="text" name="courseName" placeholder="Name" value={getcourseName} onChange={handleChangecourseName}/>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label for="courseImage" sm={2}>Course Image : </Label>
                    <Col sm={10}>
                      <Input type="text" name="courseImage" placeholder="Image" value={getcourseImage}  onChange={handleChangecourseImage}/>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label for="courseDetail" sm={2}>Text Editer : </Label>
                    <Col sm={10}>
                      <DynamicReactQuill 
                        value={getcourseDetail}
                        modules={adminDashBoardCourse.modules}
                        formats={adminDashBoardCourse.formats}
                        onChange={handleChangecourseDetail}/>
                    </Col>
                  </FormGroup>
                </Form>
                <FormGroup row>
                    <Label for="courseImage" sm={2}>Calendar : </Label>
                    <Col sm={10}>
                      <Button color='info' onClick={handleEvent} >add event </Button>
                      <p id="demo"></p>
                      <div class="Wrapper">
	<div class="Calendar">
    	<h1>January</h1>
    	<ul class="Days">
    		<li><span class="Day1">Sunday</span></li>
        	<li><span class="Day2">Monday</span></li>
        	<li><span class="Day3">Tuesday</span></li>
        	<li><span class="Day4">Wednesday</span></li>
        	<li><span class="Day5">Thursday</span></li>
        	<li><span class="Day6">Friday</span></li>
        	<li><span class="Day7">Saturday</span></li>
    	</ul>
    
    	<ul class="Dates">
    		<li></li>
        	<li></li>
        	<li><span class="N3">01</span></li>
        	<li><span class="N4">02</span></li>
        	<li><span class="N5">03</span></li>
        	<li><span class="N6">04</span></li>
        	<li><span class="N7">05</span></li>
        	<li><span class="N8">06</span></li>
        	<li><span class="N9">07</span></li>
        	<li><span class="N10">08</span></li>
        	<li><span class="N11">09</span></li>
        	<li><span class="N12">10</span></li>
        	<li><span class="N13">11</span></li>
        	<li><span class="N14">12</span></li>
        	<li><span class="N15">13</span></li>
        	<li><span class="N16">14</span></li>
        	<li><span class="N17">15</span></li>
        	<li><span class="N18">16</span></li>
        	<li><span class="N19">17</span></li>
        	<li><span class="N20">18</span></li>
        	<li><span class="N21">19</span></li>
        	<li><span class="N22">20</span></li>
        	<li><span class="N23">21</span></li>
        	<li><span class="N24">22</span></li>
        	<li><span class="N25">23</span></li>
        	<li><span class="N26">24</span></li>
        	<li><span class="N27">25</span></li>
        	<li><span class="N28">26</span></li>
        	<li><span class="N29">27</span></li>
        	<li><span class="N30">28</span></li>
        	<li><span class="N31">29</span></li>
        	<li><span class="N32">30</span></li>
        	<li><span class="N33">31</span></li>
    	</ul> 
  	</div>
</div>


<style>{`
        html, body { 
          font-family: arial, sans-serif;
          font-size: 15px;
        }
        
        #fullDiv {
          position: absolute;
          margin: 0;
          padding: 0;
          left: 0;
          top: 0;
          bottom: 0;
          right: 0;
          overflow: hidden; 
        }
        
        ul {
          margin: 0;
          padding: 0;
        }
        
        li {
          float: left;
          display: block;
          width: 14.2857%;
          text-align: center;
        }
        
        li:nth-child(n+1):nth-child(-n+7) {
          font-weight: 900;
          color: #e67e22;
        }
        
        li:nth-child(n+39), li:nth-child(n+8):nth-child(-n+16) {
          font-weight: 900;
          color: rgba(0,0,0,.3);
        }
        
        li:hover:nth-child(n+8):nth-child(-n+38), li:nth-child(17){
          border-radius: 5px;
          background-color: #1abc9c;
          color: #ecf0f1;
        }
        
        
        
      `}</style>
 

                    </Col>
                  </FormGroup>
               
                  <FormGroup check row>
                    <Col >
                      <Button type="submit" color="primary" onClick={handleSubmit} >Submit</Button>
                    </Col>
                  </FormGroup>
                  
            </Col>
          </Row>
        </Container>
      </Formm>
    </div>
  )
}


adminDashBoardCourse.modules = {
  toolbar: [
      [{ 'size': ['small', false, 'large', 'huge'] }],
      ['bold', 'italic', 'underline', 'strike'],
      [{ 'color': [] }, { 'background': [] }],
      [{'list': 'ordered'}, {'list': 'bullet'}],  
      ['link', 'image','video'],
      ['clean'],
      ['code-block']
      
    ]
}
adminDashBoardCourse.formats = [
  'bold','italic','underline','strike','size','color','background','list','indent','link','image','video','clean','code-block'
]

